@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Galada&display=swap);
body,html {
    overflow-x: hidden;
    }

.movieRow {
    margin-bottom: 30px;
}

.movieRow h2 {
    margin: 0 0 0 30px;
}

.movieRow-listarea{
    display: flex;
    flex-wrap: nowrap;
    overflow-x: hidden;
    transition: all ease 0.4s;
}



.movieRow-item {
    display: inline-block;
    width: 150px;
    /* background-color: rgba(255, 255, 255, 0.2); */
}

.movieRow-item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
    cursor: pointer;
}

.movieRow-item img:hover {
    width: 100%;
    transform: scale(1);
}

.movieRow-left,
.movieRow-right {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0 , 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.4s;
}


.movieRow-left{
    left: 0;
}
.movieRow-right {
    right: 0;
}

.movieRow:hover .movieRow-left,
.movieRow:hover .movieRow-right {
    opacity: 1;
}

@media (max-width: 760px) {
    .movieRow-listarea {
        /* Unnecessary IMHO, uncomment next line to force hidden behavior */
        overflow-x: hidden !important;
        /* Additional tunning proposed by the community */
        position: relative;
        width: 100%;
    }
    .movieRow-left,
    .movieRow-right {
        opacity: 1;
    }
}
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

footer {
  text-align: center;
  margin: 10px 0;
}

.loading {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 100;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading img {
  width: 35%;
}
.destaque {
    height: 100vh;
    margin-bottom: -150px;
}

.destaque-vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.destaque-horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
}

.destaque-name {
    font-size: 60px;
    font-weight: 700;
}

.destaque-info {
    font-size: 18px;
    font-weight: 600;
    margin-top: 15px;
}

.destaque-pontos {
    color: #46d369;

}

.destaque-pontos,
.destaque-ano,
.destaque-temporadas {
    margin-right: 15px;
    display: inline-block;
}

.destaque-desc {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
    /* Limitando textos Linhas */
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical; 
}

.destaque-buttons {
    margin-top: 10px;
}

.destaque-button1 {
    font-size: 12px;
    padding: 15px 20px;
    border-radius: 5px;
    font-weight: 700;
    margin-right: 15px;
    background-color: #FFF;
    color: #000;
    opacity: 1;
    border: none;
}

.destaque-button2 {
    font-weight: 700;
    border-radius: 5px;
    padding: 15px 20px;
    font-size: 12px;
    background-color: #333;
    color: #FFF;
    opacity: 1;  
    border: none;
}

.destaque-button1:hover, 
.destaque-button2:hover {
    opacity: 0.8;
}

.destaque-geners {
    font-size: 18px;
    color: #999;
    margin-top: 10px;
}

@media (max-width: 760px) {
    .destaque {
        height: 80vh;
        margin-bottom: -70px;
    }
    .destaque-name {
        font-size: 35px;
    }
    .destaque-pontos,
    .destaque-ano,
    .destaque-temporadas {
        font-size: 16px;
    }
    .destaque-desc {
        font-size: 14px;
        max-width: 80%;
    }
    .destaque-geners {
        font-size: 14px;
    }
}
.header {
    width: 100vw;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 999;
    height: 70px;
    display: flex;  
    justify-content: space-between;
    align-items: center;
    background: transparent;
    transition: all 1s ease;
    position: fixed;
    overflow: hidden;
}

.black {
    background-color: #141414;
}

.logo img{
    padding-left: 30px;
    width: 150px;
}
.dev {
    font-weight: 700;
    font-family: 'Galada', cursive;
    font-size: 30px;
    padding-right: 20px;
}

@media (max-width: 760px) {
    .header {
        position: fixed;
    }
    .dev {
        font-size: 12px;
    }
}
